.page {
  font-family: "Manrope", "Inter", sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 140%;
  color: $color-main;
  scroll-behavior: smooth;
  background-color: #fff;

  &:has(.menu:target) {
    overflow: hidden;

    &__header {
      margin-bottom: 64px;

      @include onTablet {
        margin-bottom: 120px;
      }
    }

    &__content {
      margin: 0;
      padding: 0;
    }

    &__menu {
      position: fixed;
      inset: 0;
    }
  }

  ::placeholder {
    color: $color-placeholder;
  }
}
